.chatsContainer {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    overflow-y: auto;
    background-color: #f5f5f5
}

.chatPreview {
    display: flex;
    flex-direction: row;
    color: black;
    background-color: white;
    transition: background-color 0.2s, color 0.2s;
}

.chatPreview:hover {
    background-color: #c6c3c7;
}

.chatPreview:hover .chatDetails, .chatMeta {
    border-bottom: 0;
}

.chatPicture {
    display: flex;
    flex-basis: 85px;
    align-items: center;
    justify-content: center;
}

.picture {
    border-radius: 50%;
}

.chatDetails, .chatMeta {
    display: flex;
    flex-direction: column;
    padding: 15px;
    border-bottom: 1px solid #e7e7e7;
    gap: 10px;
}

.chatDetails {
    flex-grow: 1;
}

.chatLastMessage, .lastMessageTime {
    color: #787878;
}

.messageState {
    align-self: flex-end;
}
